import axios from "axios";

const i = axios.create({
    baseURL: "http://127.0.0.1:8000",
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json'
    }
})

const login = async (login, password) => {
    i.post('/auth/token/login', {
        username: login,
        password: password
    }).then((response) => {
        // localStorage.setIte(response.data.csrfToken)
        console.log("login:", response);
        console.log("login:", response.data);
        console.log("login:", response.data.data.auth_token);
        return response;
    }).catch((error) => {
        console.log(error)
    })
}

const me = async () => {

    return null;
}


export const API = {
    login,
    me
}